<template>
  <v-app>
    <v-toolbar dark dense flat color="teal darken-4">
      <v-toolbar-side-icon></v-toolbar-side-icon>
      <v-toolbar-title>My App</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn flat>Link One</v-btn>
        <v-btn flat>Link Two</v-btn>
        <v-btn flat>Link Three</v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <hr>
    <v-container fluid wrap fill-height pa-0>

      <Multipane class="vertical-panes" layout="vertical">
        <!-- Elements -->
        <v-tabs dark grow class="pane" :style="{ minWidth: '12%', width: '15%', maxWidth: '30%', height: '100%' }" color="teal darken-4" slider-color="secondary">
          <v-tab>
            <span>
              <v-icon>select_all</v-icon>
              Elements
            </span>
          </v-tab>
          <v-tab-item class="tab-content">
            <Components>
            </Components>
          </v-tab-item>
        </v-tabs>
        <MultipaneResizer></MultipaneResizer>
        <!-- UI -->
        <v-tabs dark grow class="pane pane-ui" :style="{ minWidth: '400px', width: '50%', maxWidth: '70%', height: '100%' }" color="teal darken-4" slider-color="secondary">
          <v-tab>
            <span>
              <v-icon>web</v-icon>
              Design
            </span>
          </v-tab>
          <v-tab-item class="tab-content">
            <DesignTab>
            </DesignTab>
          </v-tab-item>
        </v-tabs>
        <MultipaneResizer></MultipaneResizer>
        <!-- State -->
        <v-tabs dark class="pane" :style="{ minWidth: '200px', width: '20%', maxWidth: '40%', height: '100%' }" grow color="teal darken-4" slider-color="secondary">
          <v-tab>
            <span>
              <v-icon>fas fa-cubes</v-icon>
              Data
            </span>
          </v-tab>
          <v-tab-item class="tab-content">
            <StatesBar>
            </StatesBar>
          </v-tab-item>
        </v-tabs>
        <MultipaneResizer></MultipaneResizer>
        <!-- Properties -->
        <v-tabs dark grow class="pane" :style="{ minWidth: '200px', flex: 1, height: '100%' }" color="teal darken-4" slider-color="secondary">
          <v-tab>
            <span>
              <v-icon>fas fa-list</v-icon>
              Properties
            </span>
          </v-tab>
          <v-tab-item class="tab-content">
            <PropertiesBar>
            </PropertiesBar>
          </v-tab-item>
        </v-tabs>
      </Multipane>
    </v-container>
  </v-app>
</template>

<script>
import {
  ElementModel,
  PropertyModel,
  StateModel,
  Types
} from "./ElementModel.vue";
import BaseElement from "./BaseElement.vue";
import Components from "./Components.vue";
import ContainerElement from "./ContainerElement";
import StatesBar from "./StatesBar";
import DesignTab from "./DesignTab";
import PropertiesBar from "./PropertiesBar";
import { Container } from "vue-smooth-dnd";
import { Multipane, MultipaneResizer } from "vue-multipane";

export default {
  name: "App",
  data() {
    return {
      types: Types,
      newState: new StateModel("", Types.String)
    };
  },
  computed: {
    components() {
      return this.$store.state.components;
    },
    selectedElement() {
      return this.$store.state.selectedElement;
    },
    elements() {
      return this.$store.state.elements;
    },
    states() {
      return this.$store.state.states;
    }
  },
  methods: {
    getValue(element, propertyName) {
      return this.$store.getters.getValue({
        element: this.element,
        propertyName: propertyName
      });
    },
    setValue(element, propertyName, newValue) {
      return this.$store.commit({
        type: "setValue",
        element: element,
        propertyName: propertyName,
        newValue: newValue
      });
    },
    addState() {
      this.$store.state.states.push(this.newState);
      this.newState = new StateModel("", Types.String);
    },
    addBinding(state) {
      state.bindings.push({
        bindingElement: state.newElementBinding,
        bindingInput: state.newPropertyBinding
      });
      state.newElementBinding = undefined;
      state.newPropertyBinding = "";
    }
  },
  components: {
    BaseElement,
    ContainerElement,
    Components,
    StatesBar,
    DesignTab,
    PropertiesBar,
    Multipane,
    MultipaneResizer
  }
};
</script>

<style scope>
.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.full-size {
  width: 100%;
  height: 100%;
}

body,
html {
  height: 100%;
}

.vertical-panes {
  z-index: 0;
  padding: 0px;
  margin: 0px;
  height: 100%;
  width: 100%;
}
.vertical-panes > .pane {
  text-align: left;
  padding: 0px;
  overflow: hidden;
  background: #eee;

  display: flex;
  flex-flow: column;
  height: 100%;
}

.vertical-panes > .pane ~ .pane {
  border-left: 2px solid #ccc;
}

.vertical-panes > .pane > .tabs__items {
  flex: 1 1 auto;
}

.vertical-panes > .pane-ui > .tabs__items {
  display: inherit;
}
</style>



