<template>
  <v-container fluid wrap grid-list-md pa-0>
    <v-layout row wrap>
      <Container class="components-container"
        :behaviour="'copy'"
        :group-name="'elements'"
        :get-child-payload="getPayload" style="width:100%"
        drag-class="my-drag" drop-class="my-drop">
        <v-text-field solo flat
          v-model="componentSearch"
          prepend-icon="search"
          append-icon="close"
          label="Search Element">
        </v-text-field>
        <Draggable v-for="component in components" :key="component.name">
            <v-flex xs12 sm12 ml-2 mr-2 mt-2>
              <v-card color="primary" class="white--text">
                <v-card-title primary-title>
                  <div class="title">
                    <v-icon>{{ component.icon }}</v-icon>
                    {{ component.name }}
                  </div>
                </v-card-title>
              </v-card>
            </v-flex>
        </Draggable>
      </Container>
    </v-layout>
  </v-container>
</template>

<script>
import {
  ElementModel,
  PropertyModel,
  StateModel,
  Types
} from "./ElementModel.vue";
import { Container, Draggable } from "vue-smooth-dnd";

export default {
  name: "App",
  data() {
    return {
      componentSearch: ''
    };
  },
  computed: {
    components() {
      return this.$store.state.components
        .filter(x => x.name.toLowerCase().startsWith(this.componentSearch.toLowerCase()));
    }
  },
  methods: {
    getPayload(index) {
      return this.$store.state.components[index-1];
    }
  },
  components: {
    Draggable,
    Container
  }
};
</script>

<style scoped>
.components-container {
  width: 100%;
}

.my-drag {
  transition: all 0.18s ease;
  opacity: 0.8;
  transform: rotateZ(1deg);
  font-family: "Roboto";
}

.my-drop {
  opacity: 1;
  transform: rotateZ(0deg);
  font-family: "Roboto";
}
</style>



