import Vue from "vue";
import Vuex from "vuex";
import { ElementModel, StateModel, Types } from "./ElementModel.vue";

Vue.use(Vuex);

let element1 = new ElementModel("App", "ContainerElement");
let element2 = new ElementModel("CheckboxElement1", "CheckboxElement");
let element3 = new ElementModel("CheckboxElement2", "CheckboxElement");

export const store = new Vuex.Store({
  state: {
    elements: [element1, element2, element3],
    states: [
      new StateModel("isValid", Types.Boolean),
      new StateModel("Pudin", Types.String),
      new StateModel("algo mas", Types.String)
    ],
    selectedElement: undefined,
    components: [
      {
        name: "Checkbox",
        icon: "fas fa-check-square",
        component: "CheckboxElement"
      },
      {
        name: "Button",
        icon: "fas fa-square",
        component: "ButtonElement"
      },
      {
        name: "Container",
        icon: "fas fa-th-large",
        component: "ContainerElement"
      },
      {
        name: "Input",
        icon: "fab fa-adn",
        component: "InputElement"
      },
      {
        name: "Select",
        icon: "far fa-caret-square-down",
        component: "SelectElement"
      }
    ]
  },
  getters: {
    getValue(state) {
      return (element, propertyName) => {
        let elementFound = state.elements.find(el => el.name == element.name);
        if (!elementFound) return undefined;
        let property = elementFound.properties.find(
          prop => prop.title == propertyName
        );
        if (!property) return undefined;
        return property.value;
      };
    },
    getComponent(state) {
      return element => {
        return () => import(`./${element.componentName}.vue`);
      };
    },
    getElement(state) {
      return name => {
        let elementFound = state.elements.find(el => el.name == name);
        if (!elementFound) return undefined;
        return elementFound;
      };
    },
    getProperty(state) {
      return (element, propertyName) => {
        let elementFound = state.elements.find(el => el.name == element.name);
        if (!elementFound) return undefined;
        let property = elementFound.properties.find(
          prop => prop.title == propertyName
        );
        if (!property) return undefined;
        return property;
      };
    },
    moveElement(state) {
      return (container, fromIndex, toIndex) => {
        let containerFound = state.elements.find(
          el => el.name == container.name
        );
        if (!containerFound) return;
        let property = containerFound.properties.find(
          prop => prop.title == "Elements"
        );
        if (!property) return;
        return property.value.splice(
          toIndex,
          0,
          property.value.splice(fromIndex, 1)[0]
        );
      };
    },
    addElement(state) {
      return (container, index, newValue) => {
        let containerFound = state.elements.find(
          el => el.name == container.name
        );
        if (!containerFound) return;
        let property = containerFound.properties.find(
          prop => prop.title == "Elements"
        );
        if (!property) return;
        if (!property.value.find(el => el.name == newValue.name)) {
          property.value.splice(index, 0, newValue);
        }
        if (!state.elements.find(el => el.name == newValue.name)) {
          state.elements.push(newValue);
        }
        return undefined;
      };
    },
    removeElement(state) {
      return (container, index) => {
        let containerFound = state.elements.find(
          el => el.name == container.name
        );
        if (!containerFound) return;
        let property = containerFound.properties.find(
          prop => prop.title == "Elements"
        );
        if (!property) return;
        if (index < property.value.length) {
          property.value.splice(index, 1);
        }
        return undefined;
      };
    }
  },
  mutations: {
    addProperties(state, { element, properties }) {
      let elementFound = state.elements.find(el => el.name == element.name);
      if (!elementFound) return;
      for (let property of properties) {
        elementFound.properties.push(property);
      }
    },
    setValue(state, { element, propertyName, newValue }) {
      let elementFound = state.elements.find(el => el.name == element.name);
      if (!elementFound) return;
      let property = elementFound.properties.find(
        prop => prop.title == propertyName
      );
      if (!property) return;
      property.value = newValue;
    }
  }
});

export function getValue(element, propertyName) {
  return store.getters.getValue(this.element, propertyName);
}

export function setValue(element, propertyName, newValue) {
  store.commit("setValue", {
    element: element,
    propertyName: propertyName,
    newValue: newValue
  });
}



