<template>
    <Draggable
      v-show="getValue(element, 'Visible')"
      :style="styles">
      <div v-on:click.stop="select()"
        :class="{
          'unselectable': true,
          'full-size': true,
          'draggable-item': !isSelected(),
          'selected': isSelected()
        }">
        <component
          :is="getComponent"
          :element="element">
        </component>
      </div>
    </Draggable>
</template>

<script>
import {
  ElementModel,
  PropertyModel,
  StateModel,
  Types
} from "./ElementModel.vue";
import { Draggable } from "vue-smooth-dnd";
import { getValue, setValue } from "./store";

export default {
  name: "base-element",
  props: ["element"],
  created() {
    this.$store.commit({
      type: "addProperties",
      element: this.element,
      properties: [
        new PropertyModel("Width", "10"),
        new PropertyModel("Height", "10"),
        new PropertyModel("Left", "5px"),
        new PropertyModel("Top", "5px"),
        new PropertyModel("Right", "5px"),
        new PropertyModel("Bottom", "5px"),
        new PropertyModel("Padding", "0px"),
        new PropertyModel("Visible", true)
      ]
    });
  },
  computed: {
    getComponent() {
      return this.$store.getters.getComponent(this.element);
    },
    styles: {
      get: function() {
        return {
          width: this.getValue(this.element, "Width"),
          height: this.getValue(this.element, "Height"),
          "margin-left": this.getValue(this.element, "Left"),
          "margin-top": this.getValue(this.element, "Top"),
          "margin-right": this.getValue(this.element, "Right"),
          "margin-bottom": this.getValue(this.element, "Bottom"),
          "padding": this.getValue(this.element, "Padding"),
          position: "relative"
        };
      }
    }
  },
  methods: {
    getValue,
    setValue,
    select() {
      this.$store.state.selectedElement = this.element;
    },
    isSelected() {
      return this.$store.state.selectedElement == this.element;
    },
    deselect() {}
  },
  components: {
    Draggable
  }
};
</script>

<style>
.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.full-size {
  width: 100%;
  height: 100%;
}

.selected {
  display: block;
  outline: 0;
  border: 1px solid yellow;
  margin-bottom: 2px;
  margin-top: 2px;
}

.draggable-item {
  text-align: center;
  display: block;
  background-color: #fff;
  outline: 0;
  border: 1px solid rgba(0,0,0,.125);
  margin-bottom: 2px;
  margin-top: 2px;
}
</style>



