<template>
    <v-container fluid fill-height pa-0 grid-list-md>
        <v-layout row wrap>
        <v-btn-toggle v-model="designType">
            <v-btn flat>
                <v-icon>fas fa-desktop</v-icon>
            </v-btn>
            <v-btn flat>
                <v-icon>fas fa-mobile-alt</v-icon>
            </v-btn>
            <v-btn flat>
                <v-icon>web_asset</v-icon>
            </v-btn>
        </v-btn-toggle>
        <BaseElement
            :element="getBaseElement">
        </BaseElement>
        </v-layout>
    </v-container>
</template>

<script>
import {
  ElementModel,
  PropertyModel,
  StateModel,
  Types
} from "./ElementModel.vue";
import BaseElement from "./BaseElement.vue";

export default {
  name: "App",
  data() {
      return {
          designType: 0
      };
  },
  computed: {
    getBaseElement() {
      return this.$store.getters.getElement("App");
    }
  },
  components: {
    BaseElement
  }
};
</script>

<style scoped>

</style>



