<script>

export let Types = {
  String: 1,
  Number: 2,
  Boolean: 3
};

export class ElementModel {
  name = "";
  componentName = "";
  type = Types.Number;
  properties = [];
  actions = {};

  constructor(name, componentName) {
    this.name = name;
    this.componentName = componentName;
  }

  get(propertyName) {
    return this[propertyName] ? this[propertyName].get() : undefined;
  }

  set(propertyName, value) {
    if (this[propertyName]) this[propertyName].set(value);
  }
}

export class PropertyModel {
  title = "";
  defaultValue = undefined;
  _value = undefined;
  bind = undefined;

  constructor(title, defaultValue = undefined) {
    this.title = title;
    this.defaultValue = defaultValue;
    this._value = this.defaultValue;
  }

  get value() {
    return this.bind ? this.bind.value : this._value;
  }

  set value(newValue) {
    if (this.bind) {
      this.bind.value = newValue;
    } else {
      this._value = newValue;
    }
  }
}

export class StateModel {
  name = "";
  type = Types.Number;
  _value = undefined;
  bindElement = undefined;
  bindInput = "";
  bindings = [];

  constructor(name, type) {
    this.name = name;
    this.type = type;
  }

  get value() {
    return this._value;
  }

  set value(newValue) {
    this._value = newValue;
    for (let binding of this.bindings) {
      binding.bindingElement.properties[binding.bindingInput].value = newValue;
    }
  }
}
</script>


