<template>
<div class="full-size">
    <v-container fluid grid-list-md pa-0>
        <v-text-field solo flat
          v-model="stateSearch"
          prepend-icon="search"
          append-icon="close"
          label="Search for Data">
        </v-text-field>
            <v-expansion-panel>
            <v-expansion-panel-content v-for="state in states" :key="state.name"> 
                <div slot="header">{{ state.name }}</div>
                <v-card>
                    <v-card-text style="padding: 0px">
                    <v-layout row wrap>
                        <v-flex md5>
                        Elements
                        </v-flex>
                        <v-flex md5>
                        Properties
                        </v-flex>
                    </v-layout>
                    <div class="full-size" v-for="binding in state.bindings" :key="binding">
                        <v-layout row wrap>
                        <v-flex md5>
                            <v-select solo class="elevation-0" v-model="binding.bindingElement"
                                :items="elements"
                                item-text="name"
                                :return-object="true">
                            </v-select>
                        </v-flex>
                        <v-flex md5>
                            <v-select solo class="elevation-0" v-model="binding.bindingInput"
                            v-if="binding.bindingElement"
                                :items="binding.bindingElement.properties"
                                item-text="title"
                                item-value="title"
                                :return-object="true">
                            </v-select>
                        </v-flex>
                        <v-flex md1>
                            <v-btn flat icon small color="danger">
                                <v-icon>close</v-icon>
                            </v-btn>
                        </v-flex>
                        </v-layout>
                    </div>
                    </v-card-text>
                        <v-btn outline block small color="primary" style="margin-bottom: 0px;"
                            v-on:click="addBinding(state)">
                            <v-icon>add</v-icon>
                            Add Binding
                        </v-btn>
                </v-card>
                </v-expansion-panel-content>
            </v-expansion-panel>
            <!--<v-text-field v-model="newState.name"
                placeholder="New State" solo>
            </v-text-field>
            <v-select v-model="newState.type" solo
                :items="Object.keys(types)"
                item-text="typeName">
            </v-select>-->
    </v-container>
    <v-btn block outline color="primary" style="position: absolute; bottom: 0px; margin: 0px;"
        v-on:click="addState">
        <v-icon left dark>add</v-icon>
        Add Data
    </v-btn>
</div>
</template>

<script>
import {
  ElementModel,
  PropertyModel,
  StateModel,
  Types
} from "./ElementModel.vue";
import { Container } from "vue-smooth-dnd";
import * as d3 from "d3";

export default {
  name: "StatesBar",
  data() {
    return {
      stateSearch: "",
      types: Types,
      newState: new StateModel("", Types.String)
    };
  },
  computed: {
    elements() {
      return this.$store.state.elements;
    },
    states() {
      return this.$store.state.states.filter(
        x =>
          x.name.toLowerCase().startsWith(this.stateSearch.toLowerCase())
      );
    }
  },
  methods: {
    addState() {
      this.$store.state.states.push(this.newState);
      this.newState = new StateModel("", Types.String);
    },
    addBinding(state) {
      state.bindings.push({
        bindingElement: undefined,
        bindingInput: ""
      });
      /*state.bindings.push({
        bindingElement: state.newElementBinding,
        bindingInput: state.newPropertyBinding.title
      });
      state.newElementBinding = undefined;
      state.newPropertyBinding = undefined;*/
    }
  }
};
</script>

<style scope>

</style>



